import { BrowserRouter, HashRouter, Route, Routes } from 'react-router-dom';
import { CardItem } from './page/card-item';
import { CardList } from './page/card-list';

function App() {
  return (
    // <HashRouter/> used for corect touting in GitHubPages
    // For other runs use  <BrowserRouter>
    // <HashRouter>
    <BrowserRouter basename="/codebridge">
      <Routes>
        <Route path="/codebridge" element={<CardList />} />
        <Route path="/card-item/:id" element={<CardItem />} />
      </Routes>
    </BrowserRouter>
    // </HashRouter>
  );
}

export default App;
